<template>
  <sw-page>
    <template #smart-bar-header>
      <h2>{{ $tc('sw-bundle.general.mainMenuItemGeneral') }}</h2>
    </template>

    <template #content>
      <sw-card-view>
        <sw-card :title="$tc('sw-bundle.general.welcomeCard')">
          <sw-container columns="1fr" gap="16px">
            <p>{{ $tc('sw-bundle.general.welcomeMessage') }}</p>

            <!-- Show the message from the service -->
            <p v-if="serviceMessage" style="font-weight: bold;">
              {{ serviceMessage }}
            </p>

            <sw-button
                variant="primary"
                @click="fetchServiceMessage"
                :isLoading="isLoading"
            >
              {{ $tc('sw-bundle.general.actionButton') }}
            </sw-button>
          </sw-container>
        </sw-card>
      </sw-card-view>
    </template>
  </sw-page>
</template>

<script>
const { ApiService } = Shopware.Classes;

export default {
  name: 'sw-bundle-index',
  data() {
    return {
      isLoading: false,
      serviceMessage: ''
    };
  },
  mounted() {
    // Automatically load message when page opens
    this.fetchServiceMessage();
  },
  methods: {
    fetchServiceMessage() {
      this.isLoading = true;

      // Create an ApiService instance for our custom action route
      const apiService = new ApiService(
          '/api/_action/bundle-example-step2', // base URL
          {}, // no extra headers
          Shopware.Service('loginService') // authentication
      );

      apiService.get('/message').then((response) => {
        this.serviceMessage = response.data.message;
        this.isLoading = false;
      }).catch((error) => {
        console.error('Error fetching message:', error);
        this.isLoading = false;
      });
    }
  }
};
</script>
